html, body {
    overflow-x:hidden;  /*для горизонтального*/
    min-height: 100%;
    min-width: 100%;
}

html{
    height: 1px;
}


body{
    position: relative;
    margin: 0;
    background-color: $greyBg;
}

html {
    box-sizing: border-box;
    padding-inline: 0;
}

*, *:before, *:after {
    box-sizing: inherit;
    padding-inline: 0;
    margin-block-start: 0;
    margin-block-end: 0;
}

.wrapper {
    height: 100%;
    width: 100%;
    max-width: 1800px;
    margin: 0 auto;

    box-sizing: border-box;

}

a{
    cursor: pointer;
}

button{
    background-color: inherit;
    border: none;
    padding-inline: 0;
    cursor: pointer;
}

ul{
    height: fit-content;
}

li{
    text-align: left;
}